var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _vm._m(0)}
var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_c('h2',[_vm._v("The Forces of Magic")]),_c('p',[_vm._v("\n\t\t\t\tIn our world we are familiar with the 4 fundamental forces.  The strong nuclear force, weak nuclear force, electromagnetic force,\n\t\t\t\tand gravitational force.  These forces account for everything in our universe.  In the universe of Elthelas and D&D there are\n\t\t\t\tadditional forces that act on the universe, namely, the divine force, and the arcane force.\n\t\t")]),_c('h4',[_vm._v("The Arcane Force")]),_c('p',[_vm._v("\n\t\t\t\tThe arcane force is emitted by arcanum, a rare material that is in fact a unique subatomic particle that is attracted to itself, and\n\t\t\t\treacts to intentionality (a manifestation of the divine force) by emitting forces of its own on standard matter.  Most of the\n\t\t\t\tuniverses arcanum is noninteractive, and forms a barrier between the many \"planes\" of existence.  It is theorized that in ancient\n\t\t\t\tdays the Gods built these walls of Arcanum to create greater order in the universe by separating arcanum from ordinary matter,\n\t\t\t\tallowing the divine force to act without causing unpredictable side effects.  However, philosophers have postulated that this theory\n\t\t\t\tmay have a fundamental flaw, as the large collection of divine force necessary to form a God would have created massive chain\n\t\t\t\treactions in a universe where Arcanum was more evenly distributed, likely dispersing the divine force in the process.\n\t\t")]),_c('p',[_vm._v("\n\t\t\t\tMost accessible arcanum in the universe is found in crystal-like deposits.  Sometimes these deposits are buried in rock, sometimes\n\t\t\t\tthey are free-floating, and on occasion they even fall from space.  Gravity appears to act on the deposits normally, but the other\n\t\t\t\tforces can have unpredictable results on a deposit of Arcanum.  Wizards have learned how to focus their divine soul to interact with\n\t\t\t\tarcanum deposits to produce predictable results, usually by scratching runes onto the surface of a crystal that can be read and\n\t\t\t\tmanipulated later, and sometimes by introducing other forms of matter.  The ability to produce increasingly powerful effects\n\t\t\t\trequires increasingly large deposits of arcanum (although it is not consumed in the process) however, repeated use of an arcanum\n\t\t\t\tcrystal attracts free particles of arcanum, incrementally increasing the size of the crystal, and allowing more powerful magic to be\n\t\t\t\tcast with it (in game this is represented through a wizard or other arcane casters experience level).  Certain creatures and people\n\t\t\t\thave arcanum in their blood (such as dragons and sorcerers) this allows them to produce magical effects without an outside source of\n\t\t\t\tarcanum, but because they cannot inscribe on the arcanum, the number of effects they are able to produce is limited by the power of\n\t\t\t\ttheir divine spark.  It is common practice among the bardic colleges to artificially infuse the blood with a small amount of\n\t\t\t\tarcanum so that the student can produce effects to dazzle and awe their audiences.\n\t\t")]),_c('h4',[_vm._v("The Divine Force")]),_c('p',[_vm._v("\n\t\t\t\tThe divine force is the manifestation of life itself, and can be generative or destructive, manifested in positive and negative\n\t\t\t\tenergy.  The divine force has a unique ability to interact with organic compounds, causing them to react in a predictable (though\n\t\t\t\tcomplex) manner.  Every living thing is endowed with a certain amount of divine force.  The Gods are beings endowed with massive\n\t\t\t\tamounts of divine force.  The divine force is self-replenishing with time, such that creatures that use the divine force to produce\n\t\t\t\tsome effect are drained for a time, but gain it back.  The divine force can also be granted to others (with training) although this\n\t\t\t\tweakens the one who grants it slightly.  Most mortals would die if they gave away their divine force, however, Gods give it to their\n\t\t\t\tchosen clerics and paladins regularly.  Druids and rangers draw divine force in small amounts from living things around them in the\n\t\t\t\tworld, such as plants and animals.\n\t\t")]),_c('p',[_vm._v("\n\t\t\t\tBoth the negative and positive divine forces can be used to grant or destroy life, however they naturally repel one another.\n\t\t\t\tFurther, it has been found that whether ones soul acts with the negative or positive force is determined by the sorts of things one\n\t\t\t\tdoes.  This has created the natural 'good', 'evil' dichotomy in the universe, as those who choose 'evil' acts emit a force that is\n\t\t\t\tnaturally repulsive towards those who commit 'good' acts, however in most cases the force is too weak to act at any distance from\n\t\t\t\tthe individual, and thus this repulsion is undetectable by normal means.\n\t\t")]),_c('h2',[_vm._v("The Schools of Magic")]),_c('h4',[_vm._v("Abjuration")]),_c('p',[_vm._v("\n\t\t\t\tAbjuration is the use of the arcane or divine force to create barriers. This includes creating and restoring the barriers between\n\t\t\t\tthe planes, which includes banishment effects.  Dispelling effects and anti-magic fields are fields that remove or dampen the effects\n\t\t\t\tof arcanum or the divine spark, usually by creating a barrier that emits the dampening force that moves outward.\n\t\t")]),_c('h4',[_vm._v("Conjuration")]),_c('p',[_vm._v("\n\t\t\t\tConjuration magic is the use of one of the forces to pierce the planar barriers and, usually, bring something across the gap.  This\n\t\t\t\tcan be anything from energy (such as in an orb of fire spell) to a living creature, to small amounts of divine force (such as in\n\t\t\t\thealing spells).\n\t\t")]),_c('h4',[_vm._v("Divination")]),_c('p',[_vm._v("\n\t\t\t\tDivination magic is the use of the forces to transmit energy over great distances, the energy transmitted can usually only be very\n\t\t\t\tsmall, and thus it is typically used for observational purposes.\n\t\t")]),_c('h4',[_vm._v("Enchantment")]),_c('p',[_vm._v("\n\t\t\t\tEnchantment is the use of the forces to manipulate the minds of others.  This is a delicate and difficult process, and often can\n\t\t\t\tonly be done in broad strokes.  However, most good societies consider the use of enchantment magic on unwilling individuals to be a\n\t\t\t\tviolation of personal freedom, and additionally it can leave lasting, and sometimes unpredictable, side effects.\n\t\t")]),_c('h4',[_vm._v("Evocation")]),_c('p',[_vm._v("\n\t\t\t\tEvocation is the direct application of arcane or divine force, sometimes to create other energy effects, and sometimes to simply\n\t\t\t\tapply force directly.\n\t\t")]),_c('h4',[_vm._v("Illusion")]),_c('p',[_vm._v("\n\t\t\t\tIllusion is the use of arcane or divine force to manipulate sound waves, light, and sometimes even smell.  It is a school that often\n\t\t\t\trequires a great deal of skill and finesse, but not necessarily a lot of power.\n\t\t")]),_c('h4',[_vm._v("Necromancy")]),_c('p',[_vm._v("\n\t\t\t\tNecromancy is the use of the divine force (sometimes manipulated through arcane means) to transfer the divine force from one\n\t\t\t\tcreature to another or to animate non-living creatures.\n\t\t")]),_c('h4',[_vm._v("Transmutation")]),_c('p',[_vm._v("\n\t\t\t\tTransmutation is the use of arcane or divine force to actively alter physical forms or infuse them with energy.  It is usually a\n\t\t\t\ttemporary effect as it is very difficult to overcome a living objects morphic resonance (a manifestation of the divine force that\n\t\t\t\tremembers a natural form, this is why healing spells infuse a person with a small amount of additional divine force, it strengthens\n\t\t\t\ttheir morphic resonance allowing them to heal faster).\n\t\t")]),_c('h3',[_vm._v("Rule Differences")]),_c('p',[_vm._v("\n\t\t\t\tPrepared Arcane Spells - Arcane casters that would prepare spells with a spell book in official D&D rules instead carry with them a\n\t\t\t\tpiece of arcanum crystal on which they have inscribed their known spells.  They prepare spells by communing with the arcanum crystal\n\t\t\t\tand creating resonances in the crystal that can be tapped later to cast the spell (usually through a keyword and gesture, but\n\t\t\t\tsometimes just one or the other, or sometimes also requiring a component of some sort).  Inscribing a spell onto the crystal does\n\t\t\t\tnot cost anything besides the knowledge of how the spell works.  Although scrolls are very expensive.\n\t\t")]),_c('p',[_vm._v("\n\t\t\t\tScrolls - scrolls are documents written with special ink that has been infused with arcanum crystal.  The act of casting the spell\n\t\t\t\tusually emits enough local energy to cause the paper to catch fire, thus making them impractical to reuse.  There are no divine scrolls, as divine force\n\t\t\t\tcannot be stored in ink or paper without powerful necromancy.\n\t\t")]),_c('p',[_vm._v("\n\t\t\t\tPotions - potions are drinkable liquids that have been infused with arcanum crystal already resonating with a particular spell, or infused with organic matter (often something simple and harmless, like sugar) that has been blessed by a diety.\n\t\t")]),_c('p',[_vm._v("\n\t\t\t\tWands - wands are pieces of wood or metal with a small amount of attuned arcanum crystal and a special crystal that draws in and stores ambient divine energy. By combining the two a magical effect can be produced without any input of divine energy by the user.\n\t\t")]),_c('h3',[_vm._v("Wizards")]),_c('p',[_vm._v("\n\t\tWizards don't have a spell book in the traditional sense, rather they keep an arcanum crystal (which can be integrated into other items\n\t\tsuch as a staff, wand, amulet, ring, or shield) that accumulates free arcanum over time.  Adding a new spell through means besides leveling\n\t\tup involves the purchase of arcanum dust, which is then attuned to the new spell by communing with the scroll, crystal, or other arcane\n\t\tcaster from whom you are learning the spell.  This arcanum dust then fuses with ones existing arcanum crystal.  Only spells whose attuned\n\t\tarcanum lies near the surface of the crystal can be cast, meaning changing ones prepared spells involves communing with ones crystal to\n\t\tliterally rearrange the structure of the crystal.\n\t")]),_c('p',[_vm._v("\n\t\tOne's arcanum crystal, or whatever it is attached to, acts as your arcane focus.\n\t")]),_c('p',[_vm._v("\n\t\tAttuned arcanum crystal glows faintly in colors related to the spells prepared. So a crystal of a wizard with all necromancy spells prepared\n\t\tmay be a deep black, while one of an evoker might have a red spot for his fireball spell, a white one for his ice storm spell and so on.\n\t")]),_c('h3',[_vm._v("Sorcerers and Bards")]),_c('p',[_vm._v("\n\t\tSorcerers and Bards typically have arcanum crystal that flows through their blood, although some bards have it integrated in colorful ways\n\t\ton their instruments instead.  How it got there can vary, certain sorcerers simply inherit such casting ability from their mothers,\n\t\twhen arcanum crosses the placental barrier, while others who are seeking sorcerous power simply inject themselves (or are injected) with suspended arcanum\n\t\tdust, a risky procedure that often kills the recipient.\n\t")]),_c('h3',[_vm._v("Divine Casters")]),_c('p',[_vm._v("\n\t\tDivine casters do not use arcanum, but rather borrow divine force from powerful entities, be them Gods or nature itself.\n\t")])])}]

