pions who take a more active role\n\t\tin advancing the interests of a particular nature god.\n\t\tThese clerics might hunt the evil monstrosities that\n\t\tdespoil the woodlands, bless the harvest of the faithful,\n\t\tor wither the crops of those w ho anger their gods.\n\t\t")]),_c('h4',[_vm._v("Nature Domain Spells")]),_c('table',{staticClass:"classtable table table-striped"},[_c('tr',[_c('th',[_vm._v("Cleric Level")]),_c('th',[_vm._v("Spells")])]),_c('tr',[_c('td',[_vm._v("1st")]),_c('td',[_vm._v("animal friendship, speak with animals")])]),_c('tr',[_c('td',[_vm._v("3rd")]),_c('td',[_vm._v("barkskin, spike growth")])]),_c('tr',[_c('td',[_vm._v("5th")]),_c('td',[_vm._v("plant growth, wind wall")])]),_c('tr',[_c('td',[_vm._v("7th")]),_c('td',[_vm._v("dominate beast, grasping vine")])]),_c('tr',[_c('td',[_vm._v("9th")]),_c('td',[_vm._v("insect plague, tree stride")])])]),_c('h4',[_vm._v("Acolyte of Nature")]),_c('p',[_vm._v("\n\t\t\tAt 1st level, you learn one druid cantrip of your choice.\n\t\tYou also gain proficiency in one of the following skills of\n\t\tyour choice: Animal Handling, Nature, or Survival.\n\t\t")]),_c('h4',[_vm._v("Bonus Proficiency")]),_c('p',[_vm._v("\n\t\t\tAlso at 1st level, you gain proficiency with heavy armor.\n\t\t")]),_c('h4',[_vm._v("Channel Divinity: Charm Animals and Plants")]),_c('p',[_vm._v("\n\t\t\tStarting at 2nd level, you can use your Channel Divinity\n\t\tto charm animals and plants.\n\t\t")]),_c('p',[_vm._v("\n\t\t\tAs an action, you present your holy symbol and invoke\n\t\tthe name of your deity. Each beast or plant creature that\n\t\tcan see you within 30 feet of you must make a Wisdom\n\t\tsaving throw. If the creature fails its saving throw, it is\n\t\tcharmed by you for 1 minute or until it takes damage.\n\t\tWhile it is charmed by you, it is friendly to you and other\n\t\tcreatures you designate.\n\t\t")]),_c('h4',[_vm._v("Dampen Elements")]),_c('p',[_vm._v("\n\t\t\tStarting at 6th level, when you or a creature within 3