el, you can use your Channel Divinity to create a rainstorm as an action.  The rainstorm occurs in a cylinder with a radius of 300 ft.  Vision within the rainstorm is lightly obscured beyond\n\t\t\t100 ft, fires within the area are put out, and all creatures in it have resistance to fire damage.  The rainstorm drops a cubic foot of water on each 5 ft square every round that it continues.  You\n\t\t\tcan dismiss the storm at any time, or it will end on its own after 10 minutes.\n\t\t")]),_c('h4',[_vm._v("State Change")]),_c('p',[_vm._v("\n\t\t\tAt 6th level your God grants you the ability to manipulate water around you.\n\t\t\tAs an action you can use the powers granted by your God to change a small of water to ice or steam, or ice or steam into water.  To do so, select a 5 ft cube within 60 ft of you.\n\t\t\tIf the square contains water you can choose to turn that water to ice or steam.  If you change it into ice it does 2d6 cold damage to any creature in or adjacent to that square and any\n\t\t\tcreature in that square must make a strength save against your spell save DC or be paralyzed. They can attempt the save again at the end of each turn.  If they succeed on the save the ice breaks into\n\t\t\tsmall shards.  If you turn it into steam it does 3d8 fire damage instead.  If you change ice or steam into water it has no additional adverse effect.\n\t\t\tThe transformation lasts for 1 minute, after which the effect is reversed (potentially doing damage if steam or ice that was turned into water reverts into ice or steam).  You can make\n\t\t\tthis change a number of times per day equal to your wisdom modifier.\n\t\t")]),_c('h4',[_vm._v("Cold Strike")]),_c('p',[_vm._v("\n\t\t\tAt 8th level, you gain the ability to infuse your weapon\n\t\tstrikes with cold energy. Once on each of your turns\n\t\twhen you hit a creature with a weapon attack, you can\n\t\tcause the attack to deal an extra 1d8 cold damage to\n\t\tthe target. When you reach 14th level, the extra damage\n\t