 but not the reverse.  See the Dungeon Master's Guide (p48-49)\n\t\t\t\t\t\tfor a detailed description of the Ethereal Plane.\n        ")]),_c('div',[_c('h3',[_vm._v("Astral Plane")]),_c('p',[_vm._v("\n\t\t\t\t\tPlanar Alignment: True Neutral\n\t\t\t\t")]),_c('p',[_vm._v("\n\t\t\t\tThe home of dreams and thoughts. See the Dungeon Master's Guide (p46-48) for a detailed description of the Astral Plane.\n\t\t\t")]),_c('h4',[_vm._v("Aurumal, the City of Gold")]),_c('p',[_vm._v("\n\t\t\t\tFloating freely through the Astral Sea, lies the city of Aurumal, the home of Maris, the God of Trade.  The huge metropolis is the\n\t\t\t\tfinal resting place of the devotees of Maris.  From here interplanar ships travel all the known planes (as well as unknown)\n\t\t\t\tseeking wealth through trade.  The goods they trade in run from the mundane, such as ore and timber, to the unimaginable, such\n\t\t\t\tas selling the feeling one gets when completing good works to demonic junkies in the abyss.  Maris presides over all of it, smiling\n\t\t\t\tas his servants continue their noble work through life and death.\n\t\t\t")])]),_c('div',[_c('h3',[_vm._v("Shadowfell")]),_c('p',[_vm._v("\n\t\t\t\t\tPlanar Alignment: True Neutral\n\t\t\t\t")]),_c('p',[_vm._v("\n            The Shadowfell, also called the Plane of Shadow, is a\n\t\t\t\t\t\tdimension of black, gray, and white where most other\n\t\t\t\t\t\tcolor has been leached from everything. It is a place of\n\t\t\t\t\t\tdarkness that hates the light, where the sky is a black\n\t\t\t\t\t\tvault with neither sun nor stars.  See the Dungeon Master's Guide (p51-52) for a detailed description of the Shadowfell.\n      ")])]),_c('div',[_c('h3',[_vm._v("Feywild")]),_c('p',[_vm._v("\n\t\t\t\t\tPlanar Alignment: True Neutral\n\t\t\t\t")]),_c('p',[_vm._v("\n          The Feywild, also called the Plane of Faerie, is a land of\n\t\t\t\t\tsoft lights and wonder, a place of music and death. It is a\n\t\t\t\t\trealm of everlasting twilight, with glittering faerie lights\n\t\t\t\t\tbobbing in the gentle breeze and fat fireflies buzzing\n\t\t\t\t\tthrough groves and fields.  See the Dungeon Master's Guide (p49-50) for a detailed description of the Feywild.\n      ")]),_c('h4',[_vm._v("The Fey Courts")]),_c('p',[_vm._v("\n\t\t\t\tUnlike what is described in the Dungeon Master's Guide, the Fey have many courts in the Feywild, each led by it's own queen or king\n\t\t\t\tamong the Fey.  Among them are the Blood Court, led by Sanguine, the Deep Court, led by Ara, the Sun Court, led by Firan, the Stone Court\n\t\t\t\tled by Rune, and the Sky Court, led by Mist, however there are many more, each focused on a particular aspect of the natural world.  These\n\t\t\t\tFey Kings and Queens then in turn pay some small allegiance to one or more of the Gods.  For example, Ara is at least peripherally a servant\n\t\t\t\tof Trimala.\n\t\t\t")])]),_c('div',[_c('h3',[_vm._v("Dragon Eyrie")]),_c('p',[_vm._v("\n\t\t\t\t\tPlanar Alignment: True Neutral\n\t\t\t\t")]),_c('p',[_vm._v("\n\t\t\t\t\tThe heaven of dragons, the Dragon Eyrie is a reflection of the Material Plane in stark landscapes, small peaks become massive mountains,\n\t\t\t\t\tdamp areas, swamps, and dry plains become empty deserts.  Through all of this, dragons and their kin live and rule all of the land.\n\t\t\t\t\tHumanoids of any type are only allowed as guests in the Dragon Eyrie, and if not in the presence of their sponsor are subject to\n\t\t\t\t\tdeath without remorse.  Within the Dragon Eyrie the chromatic and metallic dragons fight out their endless war for dominance between\n\t\t\t\t\tgood and evil.  The Dragon Eyrie is the home of dragon's souls.  Dragons who die in the Dragon Eyrie return to life the next dawn,\n\t\t\t\t\thowever they do not age within the Eyrie.  Similarly dragons in the material plane who die awaken in the\n\t\t\t\t\tDragon Eyrie at dawn.  Dragons who have previously died in the material plane are unable to return to the Material Plane by means of\n\