vil, Lawful Evil\n        ")]),_c('h3',[_vm._v("The Nine Hells")]),_c('p',[_vm._v("\n          Lawful Evil\n        ")]),_c('h3',[_vm._v("Acheron")]),_c('p',[_vm._v("\n          Lawful Neutral, Lawful Evil, Home of Mat'raktha\n        ")]),_c('h3',[_vm._v("Mechanus")]),_c('p',[_vm._v("\n          Lawful Neutral, Home of Tock\n        ")]),_c('h3',[_vm._v("Arcadia")]),_c('p',[_vm._v("\n            Lawful Good, Lawful Neutral, Home of Dorun\n        ")]),_c('h2',[_vm._v("The Outlands")]),_c('h3',[_vm._v("Negative and Positive Energy Planes")]),_c('p',[_vm._v("\n          Divine energy can be split into two forces, negative and positive energy.  These forces on their own can be used to produce magical effects,\n          and are used by clerics for many purposes.  The combination of these energies into a precise mix is the source of consciousness and life.\n        ")]),_c('h4',[_vm._v("Positive Energy Plane")]),_c('p'),_c('h4',[_vm._v("Negative Energy Plane")]),_c('p'),_c('h4',[_vm._v("The Confluence")]),_c('p',[_vm._v("\n          The point where the positive and negative energy planes come together is known as the confluence.  At this point sits an imposing palace\n          of white and black stone, the home of the Goddess Nera.\n        ")])])}]

